@import "../../../styles/variables";
@import "../../../styles/mixins";
@import "../../../styles/media";

.searchDropdown {
  width: 50%; }

.label {
  font-size: 10.5px;
  line-height: 12px;
  margin-bottom: 5px; }

.inputField {
  position: relative; }

.name {
  font-size: 9px;
  line-height: 13px;
  color: $dimBlack;
  background: $white;
  padding: 0px 2px;
  position: absolute;
  top: 0;
  left: 12px;
  z-index: 4;
  transform: translateY(-50%); }

.input {
  font-size: 11px;
  line-height: 13px;
  color: $mainBlack;
  width: 100%;
  padding: 8px 10.5px 9px;
  border: 1px solid $softhGrey;
  box-sizing: border-box;
  border-radius: 4px;
  transition: all $short ease;
  &:focus {
    outline: none;
    border-color: $darkBlue; }
  &.error {
    border-color: $crimson;
    box-shadow: 0px 2.5px 5.5px rgba(196, 24, 60, 0.1); }
  &::placeholder {
    color: $lightBlue;
    font-style: italic; } }

.small {
  max-width: 110.5px;
  .inputField {
    position: relative;
    &::before, &::after {
      content: "";
      width: 0;
      height: 0;
      border: 2px solid transparent;
      border-bottom-color: black;
      position: absolute;
      top: 50%;
      right: 11px;
      transform: translateY(calc(-100% - 1px));
      transition: all $short ease; }
    &::after {
      border-top-color: black;
      border-bottom-color: transparent;
      transform: translateY(calc(25%)); } }
  .input {
    padding: 8px 20px 8px 14px;
    background: transparent;
    position: relative;
    z-index: 3; }
  &.opened {
    .inputField {
      &::before {
        transform: translateY(calc(-100% - 1px)) rotate(180deg); }
      &::after {
        transform: translateY(calc(25%)) rotate(180deg); } } } }


.errorType {
  font-size: 10px;
  line-height: 11px;
  color: $crimson;
  position: absolute;
  bottom: -15px;
  left: 10.5px; }

.suggestion {
  width: 100%;
  max-height: 100px;
  overflow-y: auto;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 50;
  background: $darkWhite;
  border-radius: 4px;
  @include customScroll(); }

.item {
  font-size: 11px;
  line-height: 13px;
  padding: 6px 10.5px;
  cursor: pointer;
  transition: all $short ease;
  &:hover {
    background: $ligthGrey; } }

@include desktopMinDevice {
  .searchDropdown {
    width: auto;
    max-width: 94.5px; } }

@include mobileDevice {
  .searchDropdown {
    max-width: calc(50% - 5px);
    width: calc(50% - 5px); } }



