@import "../../../styles/variables";
@import "../../../styles/mixins";
@import "../../../styles/media";

.wrapper {
  flex-grow: 1;
  overflow: auto;
  padding: 0 20px;
  position: relative; }

.table {
  width: 100%;
  border-spacing: 0 10px;
  thead {
    padding-bottom: 20px;
    th {
      background-color: $white;
      position: sticky;
      top: 0px;
      padding-top: 10px;
      padding-bottom: 10px; } }
  td, th {
    font-size: 12px;
    text-align: center;
    width: calc(100% / 7);
    table {
      width: 100%;
      tbody {
        tr {
          td:first-of-type {
            text-align: center; } } } } }
  tr {
    td:first-of-type, th:first-of-type {
      text-align: left; } }

  tbody {
    tr:hover {
      background-color: $ligthGrey;
      cursor: pointer; } } }

.notification {
  td {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    width: 220px; } }


@include mobileDevice {
  .table {
    width: 768px;
    td, th {
      width: 300px; } } }

