@import "../../../styles/variables";

.field {
  display: flex;
  flex-direction: column; }

.title {
  font-size: 10.5px;
  line-height: 12px;
  color: $dimBlue; }

.input {
  color: $mainBlack;
  width: 100%;
  margin-top: 5px;
  border: 1px solid $softhGrey;
  background: $white;
  border-radius: 4px;
  box-sizing: border-box;
  transition: all $short ease;
  &:focus {
    outline: none;
    border-color: $darkBlue; }
  &:not(:placeholder-shown) {
    &:invalid {
      border: 0.5px solid $crimson;
      box-shadow: 0px 2.5px 5.5px rgba(196, 24, 60, 0.1); } } }
